@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@400;500&display=swap);
.baubles-bubble {
  display: flex;
  justify-content: center;
  align-items: center;
  align-self: center;
  border: 1.5px solid white;
  border-radius: 50%;
  height: 35px;
  width: 35px;
}

.baubles {
  margin-left: 1px;
  margin-top: 5px;
  font-size: 120%;
}

a {
  color: white;
}

.attributes {
  position: absolute;
  bottom: 5%;
  right: 3%;
  color: white;
  font-size: 13px;
}

.hero-container {
  position: absolute;
  top: 30%;
  left: 10%;
  display: flex;
  flex-direction: column;
}

.hero-container__text--normal {
  font-family: "Lato", sans-serif;
  font-weight: 400;
  font-size: 350%;
  text-transform: uppercase;
  word-spacing: 5px;
  letter-spacing: 1px;
  color: white;
}

.hero-container__text--typewrite {
  font-family: "Lato", sans-serif;
  font-weight: 400;
  font-size: 350%;
  color: white;
  text-transform: uppercase;
}

.typewrite {
  margin: 0 10px;
}

.hero-container__member-count {
  margin: 10px 0;
  font-size: 130%;
  color: white;
}

.hero-container__baubles-list {
  margin-top: 20px;
  display: flex;
}

.hero-container__bauble-item {
  margin-right: 15px;
}

.navigation-item {
  text-decoration: none;
  font-weight: 400;
  font-size: 105%;
  transition: border 0.2s, background 0.175s;
  padding: 15px 10px;
  white-space: nowrap;
}

nav img {
  height: 25px;
  width: auto;
  padding-left: 10px;
}

nav {
  padding-top: 30px;
  display: flex;
  transition: background 0.2s, padding 0.2s;
  font-weight: 400;
  position: fixed;
  display: flex;
  top: 0;
  left: 0;
  right: 0;
  z-index: 100;
}

.navigation--sticky {
  background-color: rgba(255, 255, 255, 0.97);
  padding: 10px 0;
  box-shadow: 0 2px 10px #afafaf;
}

.navigation--sticky .navigation-logo span {
  color: green;
}

.navigation-logo {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 5%;
}

.navigation-logo span {
  font-weight: 500;
  font-family: "Oswald", sans-serif;
  font-size: 220%;
  color: white;
  line-height: 1;
}

.navigation-list {
  flex: 1 1;
  display: flex;
  justify-content: flex-end;
  align-self: center;
}

.navigation-item {
  margin-right: 5px;
}

.navigation-item:last-child {
  margin-right: 5%;
}

footer {
  height: 100px;
  background-color: #17171a;
  position: relative;
  display: flex;
  align-self: center;
  justify-content: center;
}

footer div p {
  color: #8f8f8f;
}

.footer-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.footer-container__baubles-list {
  font-size: 150%;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  position: absolute;
  right: 3%;
  top: 15%;
}

.baubles-list__bauble-item {
  margin-right: 10px;
}

* {
  box-sizing: border-box;
  margin: 0;
  border: 0;
  font-family: "Lato", sans-serif;
}

section {
  height: 100vh;
}

.hero {
  background-image: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 100, 0, 0.6)),
    url(/DSC-react/static/media/DLSU-Manila-Campus.da70497c.jpg);

  /* There could be some potential in using this hero image */
  /* background-image: url(../../assets/images/DLSU-GDG-Hero.jpg); */
  height: 100vh;
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
}

